// Top Events
.top-events,
.filter-events {

  h3 {
    color: $gray-600
  }

  &-list {

    .row:not(:last-child) {
      margin-bottom: 37px;
    }
  }

  .nav {
    margin: 32px 0;
  }
}

// Other Events
.other-events,
.contest-events,
.filter-events {

  h3 {
    color: $gray-600;
    margin-bottom: 27px;
  }

}

// Event Filter
.filter {
  &-param {
    background-color: $white;
    border-radius: 8px;
    padding: 26px;
    margin-top: 32px;

    .title {
      display: flex;
      justify-content: space-between;

      h4 {
        color: $gray-600;
      }

      p {
        color: $primary;
        font-weight: 700;
        font-size: $font-md;
      }
    }

    .form-check {
      display: flex;
      align-items: center;
      gap: 50px;
      justify-content: space-between;

      @include respond(lg) {
        justify-content: flex-end;
      }

      &-label {
        font-size: $font-md;
        font-weight: 400;
        color: $gray-500;
      }

      &-input {

        &[type="checkbox"] {
          border-radius: 100px;
          width: 51px;
          height: 30px;
          border: none;
        }
      }
    }

  }

  .award,
  .location {
    .form-control-lg {
      border: none;
      background-color: $white;
    }
  }
}

// Events
.event-news,
.contests-news {
  .hero {
    height: calc(100vh - 100px);

    background-repeat: no-repeat;
    background-size: cover;

    background-position: center;
  }

  &-details {

    .col-lg-4 {
      margin-top: 20px;

      @include respond(md) {
        margin-top: -310px;
      }
    }

    h3 {
      color: $gray-600;

      &:nth-child(2) {
        margin-bottom: 40px;
      }
    }

    h4 {
      color: $gray-600;
      margin-bottom: 20px;
    }

    &-note {
      p {
        color: $gray-500;

        &:not(:last-child) {
          margin-bottom: 20px;
        }
      }
    }

    &-ticket {
      background-color: $white;
      border-radius: 8px;
      border: 1px solid #d0d3d8;
      padding: 32px;

      @include respond(md) {
        padding: 20px;
      }

      @include respond(lg) {
        padding: 32px;
      }

      h3 {
        margin-bottom: 5px;
        color: $gray-500;
      }

      h6 {
        color: $gray-600;
        margin-bottom: 5px;
      }

      .date,
      .location,
      .price {
        margin-bottom: 30px;

        p {
          color: $gray-500;
          margin-bottom: 12px;
        }
      }
    }

    &-policy {
      background-color: $white;
      border-radius: 8px;
      border: 1px solid #d0d3d8;
      padding: 32px;

      padding: 32px;

      @include respond(md) {
        padding: 20px;
      }

      @include respond(lg) {
        padding: 32px;
      }

      p:nth-child(1) {
        margin-bottom: 14px;
        color: $gray-600;
        font-weight: 600;
      }

      p:nth-child(2) {
        color: $gray-500;
      }
    }
  }
}

.event-news {
  .hero {
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.2)), url('../img/signup-background.png');
  }
}

.contests-news {
  .hero {
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.2)), url('../img/signup-background.png');
  }

  &-details-vote {
    background-color: $white;
    padding: 32px;
    border-radius: 8px;
    border: 1px solid #d0d3d8;

    p {
      font-size: $font-md;
      color: $gray-600;
      font-weight: 600;
      margin-bottom: 15px;
    }

    ul {
      list-style: none;

      li {
        display: flex;
        justify-content: space-between;
        align-items: center;


        font-size: $font-md;

        gap: 16px;

        & span:nth-child(1) {
          font-weight: 400;
          color: $gray-400;
        }

        & span:nth-child(2) {
          font-weight: 600;
          color: $gray-600;
        }
      }
    }
  }
}

// Event Summary
.event-summary {
  height: 100%;
  padding: 20px 0 0 0;

  .container-fluid {
    height: 100%;
    position: relative;
  }

  @include respond(md) {
    height: calc(100vh - 100px);
  }

  h2 {
    color: $gray-600;
    margin-bottom: 7px;
  }

  p {
    color: $gray-500
  }

  &-subscription {

    margin-top: 12px;

    .subcription-info {
      display: flex;
      justify-content: space-between;
      align-items: flex-start;

      background-color: $white;
      border-radius: 8px;
      padding: 34px;

      &:not(:last-child) {
        margin-bottom: 24px;
      }

      .details {
        p {
          margin-bottom: 0;
          color: $gray-500;
        }

        h2 {
          color: $gray-600;
        }
      }

      .amount {
        &-label {
          font-size: $font-md;
          font-weight: 400;
          color: $gray-600;
          margin-bottom: 8px;
        }

        select {
          width: 150px;
          margin-left: auto;
        }
      }
    }
  }
}

.home,
.contests {
  .hero {
    min-height: calc(100vh - 100px);

    display: flex;
    flex-direction: column;
    justify-content: center;

    background-repeat: no-repeat;
    background-size: cover;

    background-position: center;

    h1 {
      margin-bottom: 36px;
      color: $white;
    }

    .location {
      display: block;

      width: 100%;
      background-color: $white;
      border-radius: 8px;
      padding: 26px;

      display: flex;
      align-items: center;
      gap: 4px;
      height: 65px;

      margin-right: 50px;
      margin-bottom: 16px;

      @include respond(sm) {
        display: inline-flex;
        align-items: center;
        width: 40%;

        margin-bottom: 0;
      }

      @include respond(md) {
        width: 32%;
      }
    }

    .browse {
      color: $white;
      font-size: $h4-size;
    }
  }
}

.home {
  .hero {
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.2)), url('../img/signup-background.png');
  }
}

.contests {
  .hero {
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.2)), url('../img/contest.png');
  }
}