.form {

  &-label {
    font-size: $font-md;
    font-weight: 400;
    color: $gray-500;
    transition: all .15s ease-in-out 0s;
  }

  &-icon-left {
    position: relative;

    .form-control {
      padding-left: 52px;

    }

    .icon-left {
      position: absolute;
      top: 12px;
      left: 24px;
    }

    .form-label {
      top: 12px;
      left: 52px;
    }
  }

  &-icon-right {
    position: relative;

    .form-control {
      padding-right: 52px;
    }

    .icon-right {
      position: absolute;
      top: 12px;
      right: 24px;
    }

    .form-label {
      top: 12px;
      left: 52px;
    }
  }

  &-icon-lg {
    position: relative;

    .form-control {
      padding-left: 65px;

      &::placeholder {
        font-size: $h5-size;
        color: $gray-600;
      }
    }

    .icon {
      position: absolute;

      &-left {
        top: 12px;
        left: 24px;
      }

      &-right {
        top: 20px;
        right: 24px;
      }
    }
  }

  &-link {
    display: inline-block;
    font-size: $font-md;
    width: 100%;
    text-align: right;
  }

}