.copyrights {
  color: $gray-500;
  font-weight: 600;
  margin-top: 20px;

  @include respond(md) {
    position: absolute;
    bottom: 8px;

    margin-top: 0;
  }
}

// Order 
.order {
  .list {
    padding: 0;

    li {
      display: flex;
      justify-content: space-between;
      align-items: center;

      font-size: $font-lg;
      font-weight: 400;
      color: $gray-500;

      padding-bottom: 4px;
    }
  }

  .total {
    display: flex;
    justify-content: space-between;
    align-items: center;

    p {
      font-weight: 600;
      font-size: $font-lg;
      color: $gray-600;
    }
  }
}