.form-control,
.form-select {
  font-family: $font-family;
  font-size: $font-md;
  font-weight: 400;
  background-color: transparent;
  height: 45px;
  box-sizing: border-box;
  color: $gray-500;
  border-color: $input-border-color;

  &-md {
    height: 56px;
  }

  &-lg {
    height: 60px;
    font-size: $h5-size;
  }


  &:focus {
    box-shadow: none;
    border: 1px solid $primary;
  }

  &::placeholder {
    color: $gray-400;
  }

}

.transition {
  &+.form-label {
    position: absolute;
    top: 12px;
    left: 25px;
  }

  &:focus {
    margin-top: 40px;

    &~.form-label {
      top: -30px;
      left: 0;
    }
  }
}