.w-full {
  @include respond(md) {
    width: 100%;
  }
}

.review {
  background-color: $white;
  margin-top: 20px;

  border-radius: 0 8px 8px 0;

  &-ticket,
  &-analysis {
    height: 70px;
    padding: 13px;
  }

  &-ticket {
    background-color: $gray-600;
    border-radius: 8px 0 0 8px;

    .recent {
      h5 {
        color: $white
      }

      p {
        font-size: $font-sm;
        color: $gray-400;
      }
    }
  }

  &-analysis {

    .result {

      p {
        font-size: 12px;
        color: $gray-400;
      }

      h5 {
        color: $gray-600;
      }
    }
  }


  h5 {
    color: $gray-600;
  }
}