.card {
  overflow: hidden;
  border-radius: 8px;
  border: none;
  cursor: pointer;

  &:hover {
    box-shadow: 0 10px 10px -5px #818181;
  }

  &-body {
    padding: 0;

    margin: 20px;

    .details {
      display: flex;
      align-items: center;

      margin-bottom: 8px;

      span {
        display: inline-block;
        margin-left: 14px;

        font-size: $font-md;
        font-weight: 400;
        color: $gray-500;
      }

      img {
        width: 32px;
        height: 32px;
        border-radius: 100%;
      }
    }

    .ticket {
      margin-top: 14px;
    }

    .bottom {
      @include respond(md) {
        position: absolute;
        bottom: 0;
        left: 0;
      }
    }
  }

  &-title {
    color: $gray-600;
    margin-bottom: 16px;
  }

  &-text {
    margin-bottom: 0;
    color: $gray-500;
    font-size: $font-md;
  }

}