.form-check-input {

  &[type="checkbox"] {
    border-radius: 100%;
  }

  &[type="radio"] {
    position: relative;
    width: 24px;
    height: 24px;
    margin-right: 12px;

    &:after {
      content: "";
      width: 12px;
      display: inline-block;
      height: 12px;
      border-radius: 100%;
      background: #dadada;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }

    &:checked {
      background-color: $white;

      &::after {
        background: $secondary;
      }
    }

  }


  &:focus {
    border-color: $secondary;
    outline: none;
    box-shadow: none;
  }

  &:checked {
    background-color: $secondary;
    border-color: $secondary;
  }
}