.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-family: $font-family;
  font-weight: 400;
  height: 56px;

  font-size: $font-md;

  &-icon {
    gap: 8px;
  }

  &-primary--light {
    color: $gray-600;
    background-color: rgba($primary, 0.08);

    &:hover {
      background: $primary;
      color: $white;
    }
  }

  &-transparent {
    background-color: transparent;
    border: 1px solid $border-color;
  }

  &-xs {
    padding: 4px 8px;
    height: 25px;
    font-size: $font-sm;
  }

  &-sm {
    padding: 7px 16px;
    height: 32px;
  }

  &-lg {
    height: 60px;
  }

  &-white{
    background-color: $white;
  }
}