.footer {
  height: 100%;
  padding: 80px 0;
  background-color: $gray-600;

  @include respond(lg) {
    height: 375px;
  }

  .container {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;

    flex-direction: column;

    @include respond(lg) {
      align-items: center;
      flex-direction: row;
    }
  }

  &-detail {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    width: 60%;

    flex-direction: column;

    @include respond(lg) {
      flex-direction: row;
    }

    .contact {

      p {
        font-size: $font-md;
        color: $gray-200;
        margin-bottom: 0;
      }

      h4 {
        color: $secondary;
        margin-bottom: 16px;
      }

      .social {
        margin-bottom: 40px;

        @include respond(lg) {
          margin-bottom: 0;
        }

        p {
          margin-bottom: 11px;
        }


        &-icon {
          display: flex;
          justify-content: space-between;
          align-items: center;
          width: 100px;
        }
      }
    }

    &-link {
      width: 100%;
      margin-left: 0;

      @include respond(lg) {
        margin-left: 50px;
      }

      h4 {
        color: $white;
      }

      .nav-item {
        .nav-link {
          padding-left: 0;
        }
      }
    }
  }
}