.navbar {
  height: 100px;
  background-color: transparent;

  &-toggler {
    background-color: transparent;
    border: none;


    &:focus {
      box-shadow: none;
    }
  }

  &-nav {
    .nav-item {
      .nav-link {
        font-size: $font-md;
        font-weight: 400;
        color: $gray-300;

        &:hover,
        &:focus {
          color: $gray-200;
        }

        &.active {
          color: $gray-200;
        }
      }
    }

    .nav-user {
      padding-left: 0;

      @include respond(lg) {
        padding-left: 40px;
      }

      &-link {
        display: flex;
        align-items: center;
        gap: 12px;

        font-weight: 500;
        font-size: $font-md;
        color: $gray-600;

        img {
          border-radius: 100%;
          width: 26px;
          height: 26px;
          object-fit: cover;
        }
      }
    }
  }
}