.account {
  .container-fluid {
    position: relative;
    width: 100%;
    height: 100vh;
  }

  &-dashboard,
  &-intro {
    position: relative;
    width: 100%;
    height: 100%;
    padding: 70px 24px 0 24px;

    @include respond(sm) {
      padding: 70px 80px 0 80px;
    }

    @include respond(md) {
      padding: 70px 100px 0 100px;
    }
  }

  &-dashboard {

    .wrapper {
      width: 100%;
      height: 100%;

      margin: 0 auto;

      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }
  }

  &-intro {
    background-repeat: no-repeat;
    display: none;

    &.login {
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), url('../img/login-background.png');
    }

    &.sign {
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), url('../img/signup-background.png');
    }

    @include respond(lg) {
      display: block;
    }

    .intro {
      position: absolute;
      left: 50%;
      bottom: 70px;
      transform: translateX(-50%);
      width: 90%;
      background: rgba(255, 255, 255, 0.3);
      backdrop-filter: blur(20px);
      border-radius: 8px;
      padding: 40px 45px;
      color: $white;

      p {
        margin-top: 20px;

        .hr {
          display: inline-block;
          border-bottom: 1px solid white;
          width: 140px;
        }
      }
    }
  }

  &-footer {
    font-size: $font-md;
    font-weight: 400;
    color: $gray-600;

    a {
      padding-left: 13px;
    }
  }
}

.forget-password,
.reset-password {
  position: relative;
  width: 100%;
  height: 100%;


  padding: 70px 24px 0 24px;

  @include respond(sm) {
    padding: 70px 80px 0 80px;
  }

  @include respond(md) {
    padding: 70px 100px 0 100px;
    height: 100vh;
  }

  .wrapper {
    width: 100%;
    margin: 0 auto;

    height: 100%;

    display: flex;
    flex-direction: column;
    justify-content: center;

    @include respond(md) {
      width: 70%;
      height: 92%;
    }

    @include respond(lg) {
      width: 50%;
    }

    @include respond(xl) {
      width: 35%;
    }


    .account-footer {
      margin-top: 87px;
    }
  }
}

.reset-alert {
  position: relative;
  width: 100%;
  height: 100vh;

  padding: 70px 24px 0 24px;

  @include respond(sm) {
    padding: 70px 80px 0 80px;
  }

  @include respond(md) {
    padding: 70px 100px 0 100px;
  }

  .wrapper {
    position: absolute;
    width: 100%;

    top: 50%;
    left: 50%;

    transform: translate(-50%, -50%);

    text-align: center;

    p {
      margin: 16px 0;
      color: $gray-600;
    }
  }


  &--success {
    margin-bottom: 30px;
  }
}