.table {
  min-width: 1260px;
  margin-top: 60px;

  th,
  tr {
    height: 72px;
  }

  thead {
    tr {
      th {
        color: $gray-600;
      }
    }
  }

  > :not(caption)>*>* {
    padding: 0.9rem;
    background-color: $white;
    font-size: $font-sm;
    color: $gray-500;

    vertical-align: middle;

    .table-user {
      img {
        width: 40px;
        height: 40px;
        border-radius: 4px;

        object-fit: cover;
      }

      .detail {
        h6 {
          color: $gray-600;
        }

        p {
          font-size: $font-sm;
        }
      }
    }
  }


  // Highlight border color between thead, tbody and tfoot.
  > :not(:first-child) {
    border-top: none
  }
}