.page {
  height: 100%;
  flex-direction: column;

  @include respond(lg) {
    height: 100vh;
    flex-direction: row;
  }

  .nav-user {
    .username {
      color: $gray-200;
    }
  }

  &-information {
    width: 100%;
    padding: 0 24px;

    @include respond(lg) {
      width: 50%;
      padding: 0 100px;
    }

    &-note {
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;

      flex-direction: column;

      width: 100%;

      @include respond(lg) {
        height: calc(100vh - 100px);
      }

      p {
        color: $gray-400;
        margin-top: 32px;
      }
    }
  }


  &-images {
    width: 100%;

    @include respond(lg) {
      width: 50%;
    }

    &-details {
      position: relative;


      @include respond(lg) {
        height: 50vh;
        width: 100%;
      }


      img {
        width: 100%;
        height: 100%;

        object-fit: cover;
      }

      .text {
        padding-left: 26px;
        width: 100%;

        @include respond(lg) {
          padding-left: 70px;
          width: 70%;
        }
      }
    }
  }
}