.order-alert {
  padding: 20px 0;


  h3 {
    color: $gray-600;
  }

  h5 {
    color: $gray-500;
  }

  &-ticket {
    padding: 33px 27px;
    background-color: $white;
    border-radius: 8px;
    width: fit-content;

    margin: 60px auto;

    p {
      color: $gray-600;
      font-size: $font-md;
      margin-bottom: 0;
    }

    h6 {
      color: $secondary;
      margin-bottom: 10px;
    }

    .event {

      p {
        color: $gray-500;
        font-size: $font-md;
      }

      h4 {
        color: $gray-600;
      }
    }

    .date,
    .location {

      p:nth-child(1) {
        font-size: $font-md;
        color: $gray-500;
      }

      p:nth-child(2) {
        font-size: $font-lg;
        color: $gray-600;
      }

    }
  }
}