.contacts {
  padding: 10px 0 0 0;
  height: 100%;

  @include respond(md) {
    height: calc(100vh - 100px);
  }

  h2 {
    color: $gray-600;
    margin-bottom: 14px;
  }

  .user {
    display: flex;
    flex-direction: column;

    @include respond(md) {
      align-items: center;
      flex-direction: row;
    }

    p {
      font-size: $font-md;
      color: $gray-600;
      margin-bottom: 4px;

      @include respond(md) {
        margin-bottom: 0;
      }
    }

    &-details {
      padding: 12px 15px;
      border: 1px solid #d0d3d8;
      border-radius: 8px;

      display: flex;
      align-items: center;

      img {
        width: 26px;
        height: 26px;
        border-radius: 100%;

        object-fit: cover;
      }

      .name {
        margin: 0 15px 0 6px;
        color: $gray-600;
      }

      .link {
        font-size: $font-sm;
      }
    }
  }

  .reserve {
    .title {
      color: $gray-500;
    }

    .time {
      color: $secondary;
    }
  }

  &-information,
  .order {
    margin-top: 44px;
  }

  &-information {
    p {
      font-size: $font-md;
      color: $gray-600;
    }

    .payment {
      margin-top: 55px;

      h4 {
        color: $gray-600;
        margin-bottom: 35px;
      }

      &-type {
        padding: 14px;
        background-color: $white;
        border: 1px solid #d0d3d8;

        border-radius: 8px;

        &.active {
          border-color: $secondary;
        }

        span {
          color: $gray-600;
          font-size: $font-md;
        }

      }
    }

  }



  .order {

    @include respond(md) {
      height: 100%;
    }

    .btn-transparent {
      color: $primary;
    }
  }
}